<template>
	<div id="public-header" v-if="headerstyle.ishide != 1">
		<van-nav-bar :title="title" @click-left="onClickLeft" @click-right="onClickRight" :style="{background: headerstyle.background, color: headerstyle.color }">
			<div slot="left" v-if="back"><van-icon name="left" :style="{color: headerstyle.color}"></van-icon></div>
			<div slot="right">
				<template v-if="showh5menu">
					<i class="icon icon-sort font-20" @click="onToggleMoreMenu"></i>
				</template>
			</div>
		</van-nav-bar>
		<van-popup v-model="popupStatus.share" position="bottom" class="popup-share">
			<div class="share-wrap">
				<div class="share-title">分享</div>
				<public-share
					@copySuccess="copySuccess"
					@share="shareSuccess"
				>
				</public-share>
				<div class="share-btn" @click="onToggleShare">取消</div>
			</div>
		</van-popup>
		<div class="more-menu van-hairline--surround" v-if="popupStatus.moreMenu">
			<ul class="menus">
				<li class="menu-item" @click="onToggleShare">
					<i class="icon icon-share1"></i>
					<span class="menu-item-text">分享</span>
				</li>
				<li class="menu-item van-hairline--top" @click="onScan" v-if="util.ish5app()">
					<i class="icon icon-scanning"></i>
					<span class="menu-item-text">扫一扫</span>
				</li>
				<li class="menu-item van-hairline--top" @click="onClearCache">
					<i class="icon icon-delete"></i>
					<span>清理缓存</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import PublicShare from '@/components/share'
	export default {
		components: {
			PublicShare
		},
		name: 'PublicHeader',
		props:{
			title: String,
		},
		data() {
			return {
				back: true,
				showh5menu: 0,
				headerstyle: {
					ishide: 0,
					background: '#ff2d4b',
					color: '#fff'
				},
				popupStatus: {
					share: false,
					moreMenu: false,
				},

			}
		},

		methods: {
			onClickLeft() {
				if(!this.back) {
					return;
				}
				this.$router.back(-1);
			},
			onClickRight() {
				this.$emit('onClickRight');
			},
			onToggleShare() {
				this.popupStatus['share'] = !this.popupStatus['share'];
				if(this.popupStatus['share']) {
					this.onToggleMoreMenu();
				}
			},
			onToggleMoreMenu() {
				this.popupStatus['moreMenu'] = !this.popupStatus['moreMenu'];
			},
			copySuccess(data) {
				this.onToggleShare();
			},
			shareSuccess() {
				this.onToggleShare();
			},
			onScan() {
				this.util.scan();
				this.onToggleMoreMenu();
			},
			onClearCache() {
				this.util.clearCache();
				this.onToggleMoreMenu();
			}
		},
		mounted() {
			if(window.history.length > 1) {
				this.back = true;
			}
			var theme = this.util.getStorage('theme');
			if(theme.header) {
				this.headerstyle = theme.header;
				this.$nextTick(() => {
					var navBartitle = document.getElementsByClassName('van-nav-bar__title');
					if(navBartitle && navBartitle.length > 0) {
						navBartitle[0].style.color = this.headerstyle.color;
					}
				})
			} else {
				this.$nextTick(() => {
					var navBartitle = document.getElementsByClassName('van-nav-bar__title');
					if(navBartitle && navBartitle.length > 0) {
						navBartitle[0].style.color = '#ffffff';
					}
				})
			}
			window.addEventListener('setItem', ()=> {
				this.showh5menu = this.util.getStorage('showh5menu');
			});
		},
	}
</script>

<style>
	#public-header{
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		z-index: 2000;
	}
	#public-header>.van-hairline--bottom{
		background-color: #ff2d4b;
		color: #fff;
		font-size: 16px;
	}
	#public-header .van-icon{
		font-size: 24px;
		font-weight: bold;
		margin-right: 2px;
		color: #fff;
	}
	#public-header .van-hairline--bottom::after {
		border-bottom-width: 0px;
	}
	#public-header .popup-share{
		z-index:100000!important;
	}
	#public-header .share-wrap{
		position: relative;
		background-color: #f5f5f5;
	}
	#public-header .share-wrap .share-title{
		padding: 15px 0;
		color: #333;
		text-align: center;
		font-size: 16px;
		font-weight: 500;
	}
	#public-header .share-wrap .share-btn{
		height: 50px;
		line-height: 50px;
		text-align: center;
		background-color: #fff;
		font-size: 14px;
		color: #333;
	}


	/*分享及清理缓存按钮弹出层*/
	#public-header .more-menu{
		position: absolute;
		top: 55px;
		right: 10px;
		width: 95px;
		font-size: 14px;
		background-color: #fff;
		border-radius: 10px 5px 10px 10px;
		color: #000;
	}
	#public-header .more-menu:before{
		content: "";
		display: block;
		border-width: 10px;
		position: absolute;
		top: -16px;
		right: 5px;
		border-style:  dashed  dashed solid;
		border-color:  transparent  transparent #fff;
		font-size: 0;
		line-height: 0;
		z-index: 10;
	}
	#public-header .more-menu .menus{
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
	}
	#public-header .more-menu .menus .menu-item{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
		padding: 10px;
		width: 100%;
	}
	#public-header .more-menu .menus .menu-item .icon{
		margin-right: 5px;
	}
	#public-header .more-menu .menus .menu-item .menu-item-text{
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}

</style>



