<template>
	<div id="bind-mobile" v-if="bindMobile.show">
		<van-popup
			v-model="dialogShow"
			:close-on-click-overlay="false"
		>
			<div class="tips">为了您能接受更好的服务<br>请绑定手机号后购买</div>
			<ul class="margin-10-t">
				<li class="input-wrap">
					<input v-model="mobile" type="number" placeholder="请输入手机号" @blur="onBlur">
				</li>
				<template v-if="bindMobile.isverifymobile">
					<li class="input-wrap">
						<input class="input-captcha" v-model="captcha"  type="number" placeholder="请输入图形验证码" @blur="onBlur">
						<div class="captcha-img" @click="onRefreshCaptcha">
							<img class="img-100" :src="captcha_img" alt="">
						</div>
					</li>
					<li class="input-wrap">
						<input class="input-yzm" v-model="yzm" type="number" placeholder="请输入短信验证码" @blur="onBlur">
						<van-button size="small" type="danger" :disabled="!getCode" @click="onGetCode()">{{code.text}}</van-button>
					</li>
				</template>
				<li class="input-wrap">
					<input v-model="password" type="password" placeholder="请设置登录密码" @blur="onBlur">
				</li>
			</ul>
			<van-button size="large" type="danger" :disabled="!islegal" @click="onSubmit">绑定</van-button>
		</van-popup>
	</div>
</template>

<script>
	export default {
		props:{
			bindMobile: {
				type: Object,
				default() {
					return {
						show: 0,
						isverifymobile: 1,
					}
				}
			}
		},

		data() {
			return {
				captcha_img: '',
				dialogShow: true,
				mobile: '',
				password: '',
				captcha: '',
				yzm: '',
				getCode: true,
				islegal: true,
				code: {
					text: '获取验证码',
					downcount: 60
				},
			}
		},

		methods: {
			onGetCode() {
				if(!this.getCode) {
					return false;
				}
				if(!this.mobile) {
					this.$toast('手机号不能为空');
					return false;
				}
				if(!this.util.isValidMobile(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(!this.captcha) {
					this.$toast('请输入图形验证码');
					return false;
				}
				this.util.request({
					url: 'system/common/code',
					method: 'POST',
					data: {
						mobile: this.mobile,
						captcha: this.captcha
					},
				}).then((res) => {
					var result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					} else {
						this.getCode = false;
						this.code.text = this.code.downcount + "秒后再获取";
						var timer = setInterval(() => {
							this.code.downcount--;
							if(this.code.downcount <= 0){
								clearInterval(timer);
								this.code.text = "获取验证码";
								this.code.downcount = 60;
								this.getCode = true;
							} else {
								this.code.text = this.code.downcount + "秒后再获取";
							}
						}, 1000);
						this.$toast('验证码发送成功, 请注意查收');
					}
				})
			},

			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				if (!this.mobile) {
					this.$toast('手机号不能为空');
					return false;
				}
				if (!this.util.isValidMobile(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if (this.bindMobile.isverifymobile == 1) {
					if (!this.yzm) {
						this.$toast('请输入短信验证码');
						return false;
					}
				}
				if (!this.password) {
					this.$toast('密码不能为空');
					return false;
				} else {
					var length = this.password.length;
					if (length < 8 || length > 20) {
						this.$toast("请输入8-20位密码");
						return false;
					}
					var reg = /[0-9]+[a-zA-Z]+[0-9a-zA-Z]*|[a-zA-Z]+[0-9]+[0-9a-zA-Z]*/;
					if (!reg.test(this.password)) {
						this.$toast("密码必须由数字和字母组合");
						return false;
					}
				}
				this.islegal = false;
				var params = {
					mobile: this.mobile,
					code: this.yzm,
					password: this.password,
					force: 0,
					notCheckRepassword: 1,
				}
				this.util.request({
					url: 'wmall/member/profile/bind',
					data: params,
				}).then((res) => {
					var result = res.data.message;
					if (result.errno) {
						if (result.errno != -2) {
							this.util.$toast(result.message);
							this.islegal = true;
							return false;
						} else {
							this.dialogShow = false;
							this.$dialog.confirm({
								message: '该手机号已绑定其他账号，确定进行合并吗',
								confirmButtonColor: '#ff2d4b'
							}).then((res) => {
								params.force = 1;
								this.util.request({
									url: 'wmall/member/profile/bind',
									data: params,
								}).then((res) => {
									var result = res.data.message;
									if (result.errno) {
										this.islegal = true;
										this.util.$toast(result.message);
										return false;
									} else {
										this.util.$toast('绑定成功');
										window.bind_mobile = false;
										this.dialogShow = false;
									}
								})
							}).catch((res) => {
								this.islegal = true;
								this.dialogShow = true;
								console.log('cancel')
							})
						}
					} else {
						this.util.$toast('绑定成功');
						window.bind_mobile = false;
						this.dialogShow = false;
					}
				})
			},

			onRefreshCaptcha() {
				var that = this;
				that.util.request({
					url: 'wmall/member/profile/captcha',
				}).then((res) => {
					that.captcha_img = res.data.message.message.captcha;
				})
			},

			onBlur() {
				window.scroll(0, 0);
			},
		},
		created() {
			this.onRefreshCaptcha();
		}
	}
</script>

<style>
	#bind-mobile .van-popup{
		width: 80%;
		border-radius: 5px;
		padding: 15px;
		font-size: 15px;
	}
	#bind-mobile .tips{
		text-align: center;
		line-height: 1.5;
	}
	#bind-mobile .input-wrap{
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 50px;
		border-radius: 5px;
		border: 1px solid #EDEDED;
		margin-bottom: 10px;
	}
	#bind-mobile .input-wrap input{
		display: inline-flex;
		width: 100%;
		height: 100%;
		flex: 1;
		padding: 0 10px;
	}
	#bind-mobile .input-wrap .captcha-img{
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		width: 90px;
		height: 32px;
	}
	#bind-mobile .input-wrap .van-button{
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
	}
	#bind-mobile .input-wrap .input-captcha{
		padding-right: 110px;
	}
	#bind-mobile .input-wrap .input-yzm{
		padding-right: 98px;
	}
	#bind-mobile .van-popup{
		z-index: 10002!important;
	}
	#bind-mobile .van-overlay{
		z-index: 10001!important;
	}
</style>


