<template>
	<div id="simple-goods-handle">
		<template v-if="goods.is_options == '0' && goods.is_attrs == 0">
			<div class="cart-num" :style="{'background-color': istyle.buybtnbackground}" @click.stop="onPlus($event, goods, '', '', '1')">
				<i class="icon icon-cart" :style="{color: istyle.buybtncolor}" v-if="istyle.buybtnstyle == 1"></i>
				<i class="icon icon-plus" :style="{color: istyle.buybtncolor}" v-else-if="istyle.buybtnstyle == 2"></i>
				<div class="number" :style="{'background-color': istyle.cartnumbackground, color: istyle.cartnumcolor}" v-if="goodsNum > 0">{{goodsNum}}</div>
			</div>
		</template>
		<template v-else>
			<div class="cart-num" :style="{'background-color': istyle.buybtnbackground}" @click.stop="onSelectOption">
				<i class="icon icon-cart" :style="{color: istyle.buybtncolor}" v-if="istyle.buybtnstyle == 1"></i>
				<i class="icon icon-plus" :style="{color: istyle.buybtncolor}" v-else-if="istyle.buybtnstyle == 2"></i>
				<div class="number" :style="{'background-color': istyle.cartnumbackground, color: istyle.cartnumcolor}" v-if="goodsNum > 0">{{goodsNum}}</div>
			</div>
		</template>

		<!-- 选取规格弹出层 -->
		<van-popup v-model="popupSpecShow" class="options-popup" v-if="popupSpecShow">
			<div class="options-dialog">
				<div class="muti-close" @click.stop="popupSpecShow = false"></div>
				<div class="muti-wrap">
					<div class="muti-food-title border-1px-b">
						{{goodsActive.title}}
					</div>
					<div class="muti-cont">
						<div class='muti-cont-inner'>
							<div class='muti-sec first' v-if="goodsActive.is_options == 1">
								<div class='muti-sec-title'>规格</div>
								<div class='muti-choice'>
									<template v-for="(item, index) in goodsActive.options">
										<div :class="{selected: (goodsActive.activeOptions.optionSelected == item.id)}" @click.stop="onToggleOption(item.id, '', 'option')">{{item.name}}</div>
									</template>
								</div>
							</div>
							<div class='muti-sec' v-if="goodsActive.is_attrs == 1">
								<template v-for="(attrItem, attrIndex) in goodsActive.attrs">
									<div class='muti-sec-title'>{{attrItem.name}}</div>
									<div class='muti-choice'>
										<template v-for="(label, labelIndex) in attrItem.label">
											<div :class="{selected: (goodsActive.activeOptions.attrsSelected[attrIndex] == labelIndex)}" @click.stop="onToggleOption(attrIndex, labelIndex, 'attr')">{{label}}</div>
										</template>
									</div>
								</template>
							</div>
						</div>
					</div>
					<div class="muti-choose">
						<div class='muti-oprt'>
							<div class='muti-cart-oprt clearfix'>
								<div class='add-food'>
									<van-icon name='plus' @click.stop="onPlus($event, goodsActive, '', '', '1')" style="background-color: #6BBA50;"></van-icon>
								</div>
								<template v-if="goodsSpecNum">
									<div class='food-num'>{{goodsSpecNum}}</div>
									<div class='remove-food'>
										<div class='remove icon icon-minus' @click.stop="onMinus(goodsActive)"></div>
									</div>
								</template>
							</div>
						</div>
						<div class='muti-info'>
							<div class='muti-price'>{{Lang.dollarSign}}{{goodsActive.activeOption.price}}</div>
							<div class='muti-type'>({{goodsActive.activeOption.name}})</div>
						</div>
					</div>
				</div>
			</div>
		</van-popup>
		<!--svip会员价-->
		<van-popup v-model="popupBuySvip" class="buy-svip">
			<div class='popup-svip-title'>客官，专享价限超级会员享受哦</div>
			<div class="svip-img">
				<div class="svip-vs-left">
					<i class="icon icon-crownfill"></i>
					<div class="font-12">会员专享价</div>
					<div class="svip-vs-price"><span class="font-14">{{Lang.dollarSign}}</span>{{goods.svip_price}}</div>
				</div>
				<div class="svip-vs-right">
					<div class="svip-putong-title">普通用户价格</div>
					<div class="svip-putong-price"><span class="font-14">{{Lang.dollarSign}}</span>{{goods.origin_price}}</div>
				</div>
			</div>
			<div class="buy-svip-text">即刻加入超级会员，尊享众多会员优惠</div>
			<button class="svip-price-btn font-16 margin-15-t">
				<div class="font-bold" @click.stop="onPlus($event, goods, '', '', '3')">会员专享价购买</div>
				<div class="font-12 padding-5-t">(订单结算时成为会员享优惠)</div>
			</button>
			<van-button size="normal" class="price-btn font-16 margin-15-t" @click.stop="onPlus($event, goods, '', '', '2')">原价购买</van-button>
		</van-popup>
	</div>
</template>
<script>
	import {mapState, mapMutations} from 'vuex'
	export default {
		name: 'SimpleGoodsHandle',
		props:{
			istyle: {
				type: Object,
				default() {
					return {
						buybtnstyle: 1,
						buybtnbackground: '#ff2d4b',
						buybtncolor: '#ffffff',
						cartnumbackground: '#ffd400',
						cartnumcolor: '#3d4145'
					}
				}
			},
			goods: Object,


			from: String,
			optionId: String | Number,
			type: {
				type: String,
				default: 'takeout'
			},
		},
		data() {
			return {
				popupSpecShow: false,
				popupBuySvip: false,
				goodsActive: this.goods,
			}
		},
		computed: {
			...mapState([
				'istore', 'icart', 'cart_animate'
			]),
			goodsNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goods.id]) {
					return 0;
				}
				if(this.icart['data1'][this.goods.id][0]) {
					return this.icart['data1'][this.goods.id][0]['num'];
				}
				var num = 0;
				Object.values(this.icart['data1'][this.goods.id]).forEach((item, index) => {
					num += item.num;
				});
				return num;
			},

			goodsSpecNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if(!this.icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return this.icart['data1'][this.goodsActive.id][optionId]['num'];
			},
		},

		mounted () {

		},

		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart', 'replaceState'
			]),

			onSelectOption: function() {
				this.popupSpecShow = true;
				if(!this.goods['activeOptions']) {
					this.onGoodsOptionInit();
				}
			},

			onGoodsOptionInit: function() {
				this.goodsActive = this.goods;
				this.goodsActive['activeOptions'] = {
					option: 0,
					attrs: [],
					optionSelected: 0,
					attrsSelected: [],
					num: 0
				};
				if(this.goodsActive.is_options == 1) {
					this.goodsActive['activeOptions']['option'] = this.goodsActive.options[0]['id'];
					this.goodsActive['activeOptions']['optionSelected'] = this.goodsActive.options[0]['id'];
				}
				if(this.goodsActive.is_attrs == 1) {
					for(var i = 0; i < this.goodsActive.attrs.length; i++) {
						this.goodsActive['activeOptions']['attrs'].push(i + "s0");
						this.goodsActive['activeOptions']['attrsSelected'][i] = 0;
					}
				}
				this.onToggleActiveOption();
			},

			onToggleOption: function (optionId, optionValueID, type) {
				var goodsActive = this.goods;
				if(type == 'option') {
					goodsActive['activeOptions']['option'] = optionId;
					goodsActive['activeOptions']['optionSelected'] = optionId;
				} else {
					goodsActive['activeOptions']['attrs'][optionId] = optionId + 's' + optionValueID;
					goodsActive['activeOptions']['attrsSelected'][optionId] = optionValueID;
				}
				this.goodsActive = goodsActive;
				this.onToggleActiveOption();
			},

			onToggleActiveOption: function(option_key) {
				var goodsActive = this.goodsActive;
				if(!option_key) {
					goodsActive['activeOptionId'] = goodsActive['activeOptions']['option'];
					if(goodsActive['activeOptions']['attrs'].length > 0) {
						goodsActive['activeOptionId'] = goodsActive['activeOptionId'] + '_' + goodsActive['activeOptions']['attrs'].join('v')
					}
				} else {
					goodsActive['activeOptionId'] = option_key;
				}
				goodsActive['activeOption'] = goodsActive['options_data'][goodsActive['activeOptionId']];
				goodsActive['activeOption']['num'] = this.getSpecNum();
				this.goodsActive = Object.assign({}, goodsActive)
			},

			getSpecNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if(!this.icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return this.icart['data1'][this.goodsActive.id][optionId]['num'];
			},

			onPlus: function(event, goods, optionId, from, buysvip_status) {
				var buysvip = 0;
				if(this.icart.pindan_id == 0) {
					if(this.icart.is_buysvip == 1) {
						buysvip = 1;
					} else {
						if(this.icart.svip_buy_show >= 1 && goods.svip_buy_show == 1) {
							if(buysvip_status == 1) {
								if(this.goodsActive.activeOption) {
									if(this.goodsActive.activeOption.origin_price > 0) {
										this.goods.origin_price =  this.goodsActive.activeOption.origin_price;
									} else {
										this.goods.origin_price =  this.goodsActive.activeOption.price;
									}
									if(this.goodsActive.activeOption.svip_price > 0) {
										this.goods.svip_price = this.goodsActive.activeOption.svip_price;
									} else {
										this.goods.svip_price = this.goodsActive.activeOption.price;
									}
								}
								this.popupBuySvip = true;
								return;
							} else if(buysvip_status == 2) {
								this.popupBuySvip = false;
							} else if(buysvip_status == 3) {
								this.popupBuySvip = false;
								buysvip = 1;
							}
						}
					}
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: this.istore.id,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '+',
					is_buysvip: buysvip
				};
				var url = 'wmall/store/goods/cart';
				this.util.request({
					url: url,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					if(result.message.msg) {
						this.$toast(result.message.msg);
					}
					this.replaceCart(result.message.cart);
				})
			},
			onMinus: function(goods, optionId, from) {
				var buysvip = 0;
				if(this.icart.is_buysvip == 1) {
					buysvip = 1;
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: this.istore.id,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '-',
					is_buysvip: buysvip
				};
				var url = 'wmall/store/goods/cart';
				this.util.request({
					url: url,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					this.replaceCart(result.message.cart);
				})
			},
		}
	}
</script>

<style>
	#simple-goods-handle .cart-num{
		position: relative;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		text-align: center;
		line-height: 30px;
		background-color: #ff2d4b;
	}
	#simple-goods-handle .cart-num .icon{
		color: #fff;
		font-size: 20px;
	}
	#simple-goods-handle .cart-num .number{
		padding: 0 3px;
		font-size: 10px;
		line-height: 15px;
		color: #3d4145;
		border-radius: 15px;
		position: absolute;
		right: 0px;
		min-width: 15px;
		top: -8px;
		height: 15px;
		text-align: center;
		background-color: #FFD400;
	}

	/*选规格*/
	#simple-goods-handle .options-popup{
		max-width: 90%;
		width: 90%;
	}
	#simple-goods-handle .options-dialog{
		background-color: #fff;
		width: 100%;
	}
	#simple-goods-handle .options-dialog .muti-close{
		position: absolute;
		top: 10px;
		right: 10px;
		width: 24px;
		height: 24px;
		text-align: center;
	}
	#simple-goods-handle .options-dialog .muti-close::before,.options-dialog .muti-close::after{
		position: absolute;
		top: 10px;
		right: 1px;
		width: 20px;
		height: 1px;
		background: #666;
		content: '';
	}
	#simple-goods-handle .options-dialog .muti-close::before{
		-webkit-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		transform: rotate(45deg);
	}
	#simple-goods-handle .options-dialog .muti-close::after{
		-webkit-transform: rotate(135deg);
		-ms-transform: rotate(135deg);
		-moz-transform: rotate(135deg);
		transform: rotate(135deg);
	}
	#simple-goods-handle .options-dialog .muti-food-title{
		line-height: 44px;
		margin: 0 35px;
		font-size: 16px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		text-align: center;
		border-radius: 8px 8px 0 0;
	}
	#simple-goods-handle .options-dialog .muti-cont{
		max-height: 250px;
		overflow-y: scroll;
		overflow-x: hidden;
		-webkit-box-flex: 1;
		font-size: 14px;
		padding-bottom: 10px;
	}
	#simple-goods-handle .options-dialog .muti-cont .muti-sec .muti-sec-title{
		line-height: 20px;
		margin: 10px 0 0 15px;
	}
	#simple-goods-handle .options-dialog .muti-cont .muti-sec .muti-choice{
		margin: 0 5px;
		overflow: hidden;
	}
	#simple-goods-handle .options-dialog .muti-cont .muti-sec .muti-choice div{
		display: inline-block;
		float: left;
		margin: 10px 5px 0;
		min-width: 75px;
		padding: 0 10px;
		height: 28px;
		line-height: 26px;
		border: 1px solid #c4c4c4;
		box-sizing: border-box;
		border-radius: 14px;
		text-align: center;
		overflow: hidden;
	}
	#simple-goods-handle .options-dialog .muti-cont .muti-sec .muti-choice div.selected{
		border-color: #FF2D4B;
		color: #FF2D4B;
	}
	#simple-goods-handle .options-dialog .muti-choose{
		height: 56px;
		background-color: #f4f4f4;
		overflow: hidden;
		border-radius: 0 0 8px 8px;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-oprt{
		float: right;
		display: inline-block;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt{
		margin-top: 16px;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt .add-food,.options-dialog .muti-choose .muti-cart-oprt .remove-food{
		float: right;
		height: 35px;
		width: 35px;
		display: inline-block;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt .add-food{
		text-align: left;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt .remove-food{
		text-align: right;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt .add-food .van-icon{
		width: 30px;
		height: 30px;
		text-align: center;
		line-height: 30px;
		color: #fff;
		font-weight: bold;
		border-radius: 100%;
		position: relative;
		font-size: 16px;
		background: -webkit-gradient(linear,left top, right top,from(#FF930F),to(#FF5E4E));
		background: linear-gradient(to right,#FF930F,#FF5E4E);
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt .remove-food div{
		width: 30px;
		height: 30px;
		text-align: center;
		line-height: 30px;
		color: #fff;
		font-weight: bold;
		border-radius: 100%;
		position: relative;
		font-size: 16px;
		color: #ff2d4b;
		background-color: #fff;
		border: 1px solid #ff2d4b;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-cart-oprt div{
		height: 27px;
		line-height: 27px;
		min-width: 21px;
		text-align: center;
		float: right;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-info{
		margin-left: 15px;
		line-height: 56px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-info .muti-price{
		font-size: 24px;
		color: #FB4E44;
		vertical-align: middle;
		display: inline-block;
	}
	#simple-goods-handle .options-dialog .muti-choose .muti-info .muti-type{
		margin-left: 5px;
		font-size: 12px;
		color: #2F2F2F;
		display: inline-block;
		vertical-align: middle;
	}

	/*普通用户购买超级会员商品弹出层样式*/
	#simple-goods-handle .buy-svip{
		padding: 20px 15px;
		background-color: #fff;
		font-size: 15px;
		line-height: 1;
	}
	#simple-goods-handle .buy-svip .popup-svip-title{
		display: flex;
		align-items: center;
		font-weight: 700;
		white-space:nowrap;
		padding-bottom: 15px;
		text-align: center;
	}
	#simple-goods-handle .buy-svip .popup-svip-title:before,#simple-goods-handle .buy-svip .popup-svip-title:after{
		display: block;
		margin: 0 3.5px;
		width: 23px;
		height: 10px;
		background-image: url(../../static/img/svip-title-head.png);
		background-position: 0 0;
		background-size: 100% auto;
		background-repeat: no-repeat;
		content: "";
	}
	#simple-goods-handle .buy-svip .popup-svip-title:after{
		transform: scaleX(-1);
	}
	#simple-goods-handle .buy-svip .svip-img{
		display: flex;
		width: 100%;
		height: 73px;
		background: url(../../static/img/svip-vs-bg.png) no-repeat;
		background-size: 100% auto;
		margin-bottom: 15px;
	}
	#simple-goods-handle .buy-svip .svip-img .svip-vs-left{
		width: 50%;
		text-align: center;
		color: #FFE98B;
	}
	#simple-goods-handle .buy-svip .svip-img .svip-vs-left .icon{
		padding: 3px 0;
		font-size: 12px;
	}
	#simple-goods-handle .buy-svip .svip-img .svip-vs-left .svip-vs-price{
		font-size: 26px;
		padding: 5px 0;
	}
	#simple-goods-handle .buy-svip .svip-img .svip-vs-right{
		width: 50%;
		text-align: center;
		color: #656565;
	}
	#simple-goods-handle .buy-svip .svip-img .svip-vs-right .svip-putong-title{
		padding-top: 18px;
		font-size: 12px;
	}
	#simple-goods-handle .buy-svip .svip-img .svip-vs-right .svip-putong-price{
		font-size: 26px;
		padding: 5px 0;
	}
	#simple-goods-handle .buy-svip .buy-svip-text{
		font-size: 14px;
		color: #999;
		text-align: center;
	}
	#simple-goods-handle .buy-svip .svip-price-btn{
		display: block;
		width: 100%;
		height: 45px;
		text-align: center;
		background: linear-gradient(90deg,#faeaa7,#e0c470);
		border: none;
		border-radius: 3px;
		padding: 0;
		color: #7C4F0B;
	}
	#simple-goods-handle .buy-svip .price-btn{
		width: 100%;
		display: block;
		text-align: center;
		color: #7c7c7c;
		font-weight: 600;
	}
	#simple-goods-handle .buy-svip .van-button::before{
		border-color: transparent;
		background-color: transparent;
	}
</style>



