<template>
	<div id="public-share">
		<div class="share-list">
			<template v-if="util.ish5app()">
				<div class="share-item" @click="h5appSingleShare(0)">
					<img src="static/img/public-share-wx.png" alt="">
					<div class="text">微信好友</div>
				</div>
				<div class="share-item" @click="h5appSingleShare(1)">
					<img src="static/img/public-share-peng.png" alt="">
					<div class="text">朋友圈</div>
				</div>
			</template>
			<template v-else-if="util.isGlala()">
				<div class="share-item" @click="wxShareWebpage('session')">
					<img src="static/img/public-share-wx.png" alt="">
					<div class="text">微信好友</div>
				</div>
				<div class="share-item" @click="wxShareWebpage('timeline')">
					<img src="static/img/public-share-peng.png" alt="">
					<div class="text">朋友圈</div>
				</div>
			</template>
			<div
				class="share-item"
				v-clipboard:copy="pageUrl"
				v-clipboard:success="onCopy"
				v-clipboard:error="onError"
			>
				<img src="static/img/share-link.png" alt="">
				<div class="text">复制链接</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'PublicShare',
		props:{

		},
		data() {
			return {
				pageUrl: window.location.href
			}
		},
		methods: {
			onCopy() {
				this.util.$toast('复制成功');
				this.$emit('copySuccess', {})
			},
			onError: function (e) {
				this.util.$toast('复制失败')
			},
			h5appSingleShare(value) {
				this.util.h5appSingleShare(value);
				this.$emit('share', {})
			},
			wxShareWebpage(scene) {
				this.util.wxShareWebpage({
					scene: scene,
				});
				this.$emit('share', {})
			}
		},
		mounted() {

		}
	}
</script>

<style>
	#public-share .share-list{
		display: flex;
		flex-wrap: wrap;
		width: 100%;
	}
	#public-share .share-list .share-item{
		text-align: center;
		width: 25%;
	}
	#public-share .share-list .share-item img{
		width: 55px;
		height: 55px;
		border-radius: 100%;
	}
	#public-share .share-list .share-item .text{
		font-size: 14px;
		color: #666;
		padding: 10px 0;
	}
</style>


