<template>
	<div id="navigator" :style="{right: (menu && menu.position && menu.position.right) ? menu.position.right : '10px', left: (menu && menu.position && menu.position.left) ? menu.position.left : 'inherit', bottom: (menu && menu.position && menu.position.bottom) ? menu.position.bottom : '150px'}">
		<div class="open" @click="onToggleShow" v-if="!show">快捷<br>导航</div>
		<div class="icon icon-close"  @click="onToggleShow" v-else></div>
		<div class="navs" :class="{show: show}">
			<div
				class="nav-item"
				:style="{top: (show ? (-key-1) * 50 + 'px' : '0px'), 'background-color' : menu.params.navstyle == 1 ? 'transparent' : menu.css.iconBackgroundColor}"
				@click="util.jsUrl(item.link)"
				v-for="(item, index, key) in menu.data" :key="index"
			>
				<template v-if="menu.params.navstyle == 1">
					<div class="img-wrap">
						<img class="img-100" :src="item.img" alt="">
					</div>
				</template>
				<template v-else>
					<i class="icon font-16" :class="item.icon" :style="{color: menu.css.iconColor}"></i>
					<span class="nav-text font-12 margin-5-t" :style="{color: menu.css.textColor}" v-if="item.text">{{item.text}}</span>
				</template>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			menu: {
				type: Object,
				default() {
					return {
						css: {},
						params: {},
						data: {},
						position: {
							left: 'inherit',
							bottom: '150px',
							right: '10px'
						}
					}
				}
			}
		},
		data() {
			return {
				show: false
			}
		},
		methods: {
			onToggleShow() {
				this.show = !this.show;
			}
		},
	}
</script>

<style scoped>
	#navigator{
		position: fixed;
		z-index: 1999;
		bottom: 150px;
		right: 10px;
		width: 45px;
		height: 45px;
		border-radius: 50%;
		background-color: rgba(0, 0, 0, 0.5);
		color: #fff;
		text-align: center;
	}
	#navigator .open, #navigator .icon-close, #navigator .nav-item{
		width: 100%;
		height: 100%;
		border-radius: 50%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		font-size: 12px;
	}
	#navigator .open{
		line-height: 1.2;
	}
	#navigator .open, #navigator .icon-close{
		position: absolute;
		z-index: 10;
	}
	#navigator .icon-close{
		font-size: 24px;
		background-color: #ff2d4b;
	}
	#navigator .navs{
		width: 100%;
		height: 100%;
	}
	#navigator .navs .nav-item{
		position: absolute;
		z-index: 5;
		top: 0px;
		background-color: rgba(0, 0, 0, 0.5);
		font-size: 20px;
		color: #fff;
		transition: all .4s;
		transform: rotate(-180deg);
		opacity: 0;
	}
	#navigator .navs .nav-item .img-wrap{
		width: 100%;
		height: 100%;
		font-size: 0px;
		border-radius: 50%;
		overflow: hidden;
	}
	#navigator .navs.show .nav-item{
		transform: rotate(0deg);
		opacity: 1;
	}
</style>


